<template>
  <v-card
    elevation="8"
    class="departamento-auth__login"
  >
    <div class="departamento-auth__login-wrapper">
      <v-card-title
        class="pa-0 departamento-auth__login_heading"
      >
        <h2>Увійдіть у систему</h2>
      </v-card-title>
      <v-container>
        <v-form
          ref="loginForm"
        >
            <v-row>
              <v-text-field
                outlined
                dense

                label="Логін"
                prepend-inner-icon="mdi-account-circle"
                v-model="user.email"

                :class="{'auth-input__error': error}"
                :error="error"
                :disabled="loading"
                :rules="[rules.login.required, rules.email]"
              ></v-text-field>
            </v-row>
            <v-row>
              <v-text-field
                outlined
                dense

                type='password'
                label="Пароль"
                prepend-inner-icon="mdi-lock"
                v-model="user.password"
                
                :class="{'auth-input__error': error}"
                :error="error"
                :disabled="loading"
                :rules="[v => !!v ]"
              ></v-text-field>
            </v-row>
            <v-row
              v-if="error"
              justify="center"
              class="pa-0 ma-0"
            >   
              <error-text>
                Помилка авторізації!
              </error-text>
            </v-row>
            <v-row
              justify="center"
            >
              <base-button
                v-on:custom-click="logIn"
                :loadingState="loading"
              >
                Увійти
              </base-button>
            </v-row>
        </v-form>
      </v-container>
    </div>
  </v-card>
</template>
<script>
import BaseButton from '@/components/BaseComponents/BaseButton.vue'
import BaseErrorText from '@/components/BaseComponents/BaseErrorText.vue';

import {mapActions} from 'vuex'

export default {
  name: 'AuthLogin',
  data() {
    return {
      user: {
        email: '',
        password: ''
      },
      rules: {
        login: {
          required: value => !!value
        },
        // todo: refactor vuetify validation
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value)
        }
      }
    }
  },
  components: {
    BaseButton,
    ErrorText: BaseErrorText
  },
  methods: {
    logIn(){
      if(this.$refs.loginForm.validate()){
        this.authUser(this.user)
          .then(() => {
            this.$router.push('/auth/role')
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    ...mapActions({
      authUser: 'auth/verifyUserLogin'
    })
  },
  computed: {
    error() {
      return this.$store.state.auth.authError
    },
    loading(){
      return this.$store.state.auth.isLoading
    }
  }
}
</script>